//== Scaffolding
//
//## Settings for some of the most global styles.

// $bg-:    linear-gradient(45deg,#FA6C41,#FAA264);
//** Background color for `<body>`.
$body-bg: #fff;

//** //Navbar//
$font-color-primary: #333;
//** tab//
$font-color-normal: #666;
//** //
$font-color-sm: #999;
//** #cccccc  /
$font-color-sm-x: #ccc;

$font-color-sm-xx: #e6e6e6;
//** Global text color on `<body>`.
$text-color: $font-color-primary;

//border 
$border-color: #e6e6e6;

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif: 'PingFangSC-Regular', 'sans-serif';
$font-family-base: $font-family-sans-serif;
//**  16*$px ,px  $px
$px: 2px;
$font-size-base: 14 * $px;

$font-size-h1: 18 * $px;
$font-size-h2: 16 * $px;
$font-size-h3: 14 * $px;
$font-size-h4: 13 * $px;
$font-size-h5: 12 * $px;
$font-size-h6: 10 * $px;

//** Unit-less `line-height` for use in components like buttons.
$line-height-base: 1.428571429; // 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed: floor(
  ($font-size-base * $line-height-base)
); // ~20 * $px

//== Components
//
//## Define common padding and border radius sizes and more.
// 12  6
// radius 2
//
$padding-base: 12 * $px;
$padding-small: 6 * $px;
$padding-large: 18 * $px;

$border-radius-base: 8 * $px;
$border-radius-large: 12 * $px;
$border-radius-small: 4 * $px;

//== button
//
//## 

$btn-border-radius: 0 * $px !default; 
